/* Sidebar
----------------------------------------------------------- */
.nav-menu {
    width: 250px;
    align-self: stretch;
    padding-left: 40px;
    padding-top: 10px;
    background: $bg-nav-menu;
}

.switch-close {
    @include size(17px,15px);
    display: block;
    margin-left: auto;
    margin-right: 15px;

    &:before {
        content: url(../img/icon-switch.png);
    }
}

.menu-list {
    list-style: none;

    &_i { /*menu-list_item*/
        margin-top: 20px; 
    }

    &_i:hover {
        background: #2c536d;
    }

    &_i.__active &_i_lk { /*menu-list_item__active menu-list_item_link*/
        color: $blue-1;
        background: $white;
    }

    &_i_lk { /*menu-list_item_link*/
        display: block;
        padding: 10px;
        font-size: $ft-size-large;
        color: $ft-color-lk-nav-menu;
    }

    &_i_lk:before { /*menu-list_item_link*/
        vertical-align: middle;
        padding-right: 20px;
    }
}

.ic { /*icon with link */
    &-home:before {
        content: url(../img/icon-home.png);
    }

    &-people:before {
        content: url(../img/icon-people.png);
    }

    &-company:before {
        content: url(../img/icon-company.png);
    }

    &-message:before {
        content: url(../img/icon-message.png);
    }

    &-scheduling:before {
        content: url(../img/icon-profile.png);
    }

    &-user:before {
        content: url(../img/icon-user.png);
    }

    &-support:before {
        content: url(../img/icon-help.png);
    }
}
/* /Sidebar
----------------------------------------------------------- */