/*--------------------------------------------------------------
* Grid
--------------------------------------------------------------*/
.wrapper {
	@include center-block;
	position: relative;
	width: 1440px;
	margin: 0 auto;
}

.left-col {
	float: left;
	width: 640px;
}

.right-col {
	float: left;
	width: 310px;
}

.offset-right {
	margin-left: 100px;
}