/*--------------------------------------------------------------
* Forms
--------------------------------------------------------------*/
.field-it {  /*field-input*/
	position: relative;
	
	& .it {
		padding-right: 50px;
	}
}

.it { /*input*/
	@include border-radius(20);
	@include size(100%,40px);
	
	display: block;
	padding: $padding-base-vertical $padding-base-horizontal;
	font-size: 14px;
	color: #a8acb2;
	outline: 0;
}

.field-it_btn { /*field-input-button*/
	position: absolute;
	top: 0;
	right: 0;
	display: block;
	margin-right: 15px;
	margin-top: 5px;
	font-size: 17px;
	text-align: center;
	color: #ccc;
}
