/*--------------------------------------------------------------
* Main content
--------------------------------------------------------------*/

.flex-container {
    display: flex;
    flex-flow: row wrap;
    min-height: 805px;
}

.main-content {
    flex: 3 0;
    background-color: $bg-main-section;
}

.content {
    position: relative;
    width: 1160px;
    margin-top: 10px;
    padding: 10px 50px;
    height: 600px;
    overflow: auto;

    /*custom scrollbar*/
    &::-webkit-scrollbar-track {
        border: 1px solid #c6ccd5;
        background-color: #fff;
    }

    &::-webkit-scrollbar {
        width: 8px;
    }

    &::-webkit-scrollbar-thumb {
        background-color: #c6ccd5;
    }
    /*/custom scrollbar*/
}

/* Top line
------------------------------------------- */
.top-line {
    @include size(100%,40px);
    background-color: $bg-top-line;
    padding: 0 50px;

    &_switch { /*top-line_switch*/
        @include inline-block;
        float: right;
        padding: 13px 0 13px 30px;
        border-left: 3px solid $blue-3;
    }

    &_switch_lk:before { /*top-line_switch_link*/
        @include size(14px,11px);
        content: url(../img/switch-list.png);
    }
}

.nav-tabs_i { /*nav-tabs_item*/
    float: left;
    height: 40px;
    padding: 13px 15px;

    &:hover {
        background-color: #284558;
    }

    &.__active { /*nav-tabs_item__active*/
        color: $blue-4;
        background-color: $white;
    }

    &.__active &_lk { /*nav-tabs_item__active nav-tabs_item_link*/
        color: $blue-4;
    }

    &_lk { /*nav-tabs_item_link*/
        font-size: $ft-size-base;
        color: $white;
    }
}
/* /Top line
------------------------------------------- */

/* Left column
------------------------------------------- */
.content-title {
    position: relative;
    z-index: 1;
    padding: 10px;
    font-size: $ft-size-large;
    text-align: center;
    color: $title-ft-color;

    &:before {
        position: absolute;
        top: 50%;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: -1;
        width: 100%;
        margin: 0 auto;
        border-top: 1px solid #dfdfdf;
        content:"";
    }

    & span {
        padding: 0 15px;
        background: #fff;
    }
}

.content-table {
    border-collapse: separate;

    & .label {
        font-size: $ft-size-small;
        text-align: right;
        color: $label-ft-color;
    }

    & .value {
        color: $main-ft-color;
    }
}

/*block-user*/
.block-user {
    margin-bottom: 15px;

    &_avatar,
    &_list {
        @include inline-block;
    }
}

.block-user_list {
    margin-left: 25px;

    &_name {
        font-size: $ft-size-large;
        color: #4c484c;
    }

    &_post {
        color: $grey;
        margin-top: 7px;
    }

    &_address {
        font-size: $ft-size-small;
        color: $grey;
        margin-top: 13px;
    }
}
/*/block-user*/

/*block-summary*/
.features {
    border-spacing: 10px 30px;
}
/*/block-summary*/

/*block-bio*/
.block-bio p {
    line-height: 1.428571429;
    color: $main-ft-color;
}
/*/block-bio*/

/* /Left column
------------------------------------------- */

/* Right column
------------------------------------------- */
.edit-profile {
    @include size(100px,80px);
    position: relative;
    display: block;
    margin-left: auto;
    margin-bottom: 15px;
    text-align: right;

    &_lk {
        font-size: $ft-size-small;
        color: $blue-1;
    }

    &_lk:before {
        position: absolute;
        bottom: 0;
        top: -4px;
        left: 10px;
        padding-right: 5px;
        content: url(../img/icon-edit.png);
    }
}

/*block-address*/
.address {
    border-spacing: 10px 15px;

    & .label {
        min-width: 75px;
    }
}

.address_email .value,
.address_private-email .value {
    color: #47bae6;
}
/* /Right column
------------------------------------------- */